import '../style/Control.css';
import { useState } from 'react';
import { Link } from 'react-router-dom';
import MyDrawer from '../Drawer';

function DoorLock() {

  const newWin1 = () =>{
   const w = window.open('about:blank');
   w.location.href='/sixpin00/oldglx/Pub';
  };
  const newWin2 = () =>{
    const w = window.open('about:blank');
    w.location.href='/sixpin00/oldglx/Pri';
   };

  return (
    <div>
      <MyDrawer Login = {true}/>
      <div className='controlCenter'>
        <h2></h2>
        <h3></h3>
        <button onClick = {newWin1}></button>
        <button onClick = {newWin2}></button>
        {/*  */}
        {/* <h3></h3> */}
        
      </div>
    </div>      
  );
}

export default DoorLock;
